import React from "react";
import { Grid, Modal, Typography, MenuItem } from "@mui/material";
import { Box } from "@mui/system";
import MainButton from "./MainButton";
import BookIcon from "@mui/icons-material/Book";
import { styled } from "@mui/system";
import { TextField } from "@mui/material";
import { serviceValues } from "./serviceValues";
import SendIcon from "@mui/icons-material/Send";

import AdapterDateFns from "@date-io/date-fns";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import MobileDateTimePicker from '@mui/lab/MobileDateTimePicker';

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%,-50%)",
  width: "90%",
  bgcolor: "#FAFAFA",
  boxShadow: 24,
  overFlow: "visible",
};

const CssTextField = styled(TextField)({
  "& label.Mui-focused": {
    color: "var(--primary)",
  },
  "& .MuiInput-underline:after": {
    borderBottomColor: "green",
  },
  "& .MuiOutlinedInput-root": {
    "& fieldset": {
      borderColor: "#606060",
    },
    "&:hover fieldset": {
      borderColor: "var(--primary)",
    },
    "&.Mui-focused fieldset": {
      borderColor: "var(--primary)",
    },
  },
  "& .MuiInputLabel-root": {
    color: "rgba(10, 10, 10, 0.6)",
  },
  "& .MuiInputBase-root": {
    color: "rgba(10, 10, 10, 0.6)",
  },
  "& .MuiFormHelperText-root": {
    color: "rgba(255, 255, 255, 0.6)",
  },
});

const MyModal = () => {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [valueDate, setValueDate] = React.useState(new Date('2014-08-18T21:11:54'));

  return (
    <div>
      <MainButton
        onclick={handleOpen}
        buttonName="HACER RESERVA"
        icon={<BookIcon />}
      />
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Grid container>
            <Grid
              className="myScroll"
              item
              xs={12}
              md={7}
              sx={{
                overflowY: "scroll",
                height: "700px",
                p: 3,
              }}
            >
              <Typography
                variant="h4"
                sx={{ color: "black", fontWeight: "bolder", fontSize: "2.6em" }}
              >
                Haz tu reserva
              </Typography>
              <Typography
                variant="h5"
                sx={{ color: "black", fontWeight: "bolder", fontSize: "1.9em" }}
              >
                Facil y rapido!
              </Typography>
              <Typography variant="caption" sx={{ color: "#505050" }}>
                Lorem ipsum dolor sit amet consectetur sumenda in odio facilis,
                distinc
              </Typography>

              <Box
                component="form"
                sx={{
                  "& .MuiTextField-root": {
                    marginTop: 1,
                    marginBottom: 1,
                    width: "100%",
                  },
                }}
                autoComplete="off"
              >
                <div className="leftSide">
                  <CssTextField
                    id="nameClient"
                    placeholder="Nombre y Apellido completo"
                    label="Nombre"
                    name="nameClient"
                  />

                  <CssTextField
                    id="emailClient"
                    placeholder="Correo"
                    label="Correo"
                    name="emailClient"
                  />

                  <CssTextField
                    id="phoneClient"
                    placeholder="Numero Celular "
                    label="Telefono celular"
                    name="Numero celular"
                    type="tel"
                  />

                  <CssTextField
                    id="amountPeople"
                    placeholder="Cantidad de personas"
                    label="Cantidad de personas"
                    name="amountPeople"
                    type="number"
                  />
                  <CssTextField
                    id="selectService"
                    select
                    defaultValue={0}
                    label="Select"
                    name="selectService"
                  >
                    {serviceValues.map((option) => {
                      return (
                        <MenuItem
                          disabled={option.disabled}
                          key={option.value}
                          value={option.value}
                        >
                          {option.label}
                        </MenuItem>
                      );
                    })}
                  </CssTextField>

                  <LocalizationProvider dateAdapter={AdapterDateFns}>
                    <MobileDateTimePicker
                      label="Date mobile"
                      value={valueDate}
                      onChange={(newValue)=>{
                        setValueDate(newValue)
                      }}
                      renderInput={(params)=> <CssTextField {...params} />}
                    />
                  </LocalizationProvider>
                  <MainButton
                    buttonName="Enviar"
                    icon={<SendIcon />}
                    type="submit"
                  />
                </div>
              </Box>
            </Grid>
            <Grid
              item
              xs={12}
              md={5}
              sx={{ textAlign: "center", bgcolor: "var(--primary)" }}
            >
              <div
                className="patternReservas"
                style={{
                  backgroundRepeat: "repeat",
                  backgroundImage: `url(${
                    require("../assets/images/patterReservas.png").default
                  })`,
                }}
              ></div>
            </Grid>
          </Grid>
        </Box>
      </Modal>
    </div>
  );
};

export default MyModal;
